import React, { Fragment } from "react";
import { withStateHandlers } from "recompose";

import { Row, Column } from "components/Grid.tsx";
import arrowDown from "assets/img/icons/gray/arrow-down.svg";

import {
  // Desktop styled components
  Container,
  Title,
  Subtitle,
  CenteredContainer,
  Breakdown,
  BreakdownRow,
  BreakdownCell,
  // Mobile styled components
  MobileRow,
  MobileTitle,
  TitleText,
  MobileWrapper,
  Icon,
  ProductTitle,
  ProductDescription,
  ExpansionWrapper,
} from "./elements";

import { data } from "./messages.js";

const Desktop = () => (
  <Breakdown>
    <BreakdownRow isTopRow>
      <BreakdownCell isHeader index />
      <BreakdownCell isHeader />
      <BreakdownCell isHeader isHighlighted isExtendedTop>
        Brex Card
      </BreakdownCell>
      <BreakdownCell isHeader>Working Capital Loan</BreakdownCell>
      <BreakdownCell isHeader>Credit Card</BreakdownCell>
    </BreakdownRow>
    {data.map((item, i) => (
      <BreakdownRow key={`breakdown-row-${item.prop}`}>
        <BreakdownCell index>{`0${i + 1}`}</BreakdownCell>
        <BreakdownCell isLabel>{item.prop}</BreakdownCell>
        <BreakdownCell isHighlighted>{item.brex}</BreakdownCell>
        <BreakdownCell>{item.WCL}</BreakdownCell>
        <BreakdownCell>{item.CC}</BreakdownCell>
      </BreakdownRow>
    ))}

    <BreakdownRow isBottomRow>
      <BreakdownCell index />
      <BreakdownCell />
      <BreakdownCell isHighlighted isExtendedBottom />
      <BreakdownCell />
      <BreakdownCell />
    </BreakdownRow>
  </Breakdown>
);

// MOBILE VERSION BELOW: Currently not exporting it, but will do so soon with a
// simple toggle based on screen width breakpoints. The component isn't perfect yet
// either

const _Category = ({ category, index, open, toggleOpenState }) => (
  <MobileRow>
    <MobileTitle onClick={() => toggleOpenState()}>
      <Icon alt="arrow down" src={arrowDown} />
      <TitleText>{`0${index + 1}`}</TitleText>
      <TitleText primary>{category.prop}</TitleText>
    </MobileTitle>
    {open && (
      <ExpansionWrapper>
        <ProductTitle>Brex</ProductTitle>
        <ProductDescription>{category.brex}</ProductDescription>
        <ProductTitle>Working Capital Loan</ProductTitle>
        <ProductDescription>{category.WCL}</ProductDescription>
        <ProductTitle>Credit Card</ProductTitle>
        <ProductDescription>{category.CC}</ProductDescription>
      </ExpansionWrapper>
    )}
  </MobileRow>
);

const Category = withStateHandlers(
  { open: false },
  {
    toggleOpenState: ({ open }) => () => ({
      open: !open,
    }),
  },
)(_Category);

const Mobile = () => (
  <MobileWrapper>
    {data.map((category, index) => (
      <Category category={category} index={index} key={category.prop} />
    ))}
  </MobileWrapper>
);

const BreakdownComponent = () => (
  <Fragment>
    <Container id="breakdown">
      <Row padding={30} gutterWidth={30}>
        <Column span={12}>
          <CenteredContainer>
            <Title>The smartest way to finance for online brands</Title>
            <Subtitle>
              Against merchant cash advances, credit lines, bank or working
              capital loans, theres just no comparison. Well prove it.
            </Subtitle>
            <Desktop />
          </CenteredContainer>
        </Column>
      </Row>
    </Container>
    <Mobile />
  </Fragment>
);

export default BreakdownComponent;
