import React from "react";
import { DateTime } from "luxon";

import {
  Container,
  Dates,
  DateLabelWrapper,
  DayNumber,
  Date,
  BarBackground,
  BarProgress,
  SpendWrapper,
  SpendAmount,
  SpendLabel,
  DeadlineLabel,
} from "./elements";

const DateLabel = ({ date, dayNumber, alignItems }) => (
  <DateLabelWrapper alignItems={alignItems}>
    <DayNumber>{`DAY ${dayNumber}`}</DayNumber>
    <Date>{date}</Date>
  </DateLabelWrapper>
);

const thirtyDaysBack = DateTime.local()
  .minus({ days: 30 })
  .toLocaleString(DateTime.DATE_FULL);

const today = DateTime.local().toLocaleString(DateTime.DATE_FULL);

const thirtyOneDaysForward = DateTime.local()
  .plus({ days: 31 })
  .toLocaleString(DateTime.DATE_FULL);

const dates = [
  {
    date: thirtyDaysBack,
    dayNumber: 1,
    alignItems: "flex-start",
  },
  {
    date: today,
    dayNumber: 30,
    alignItems: "center",
  },
  {
    date: thirtyOneDaysForward,
    dayNumber: 61,
    alignItems: "flex-end",
  },
];

const PaymentSlider = () => (
  <Container>
    <Dates className="hidden-in-percy">
      {dates.map((dateObj) => (
        <DateLabel
          date={dateObj.date}
          dayNumber={dateObj.dayNumber}
          alignItems={dateObj.alignItems}
          key={dateObj.dateNumber}
        />
      ))}
    </Dates>
    <BarBackground>
      <BarProgress />
    </BarBackground>
    <Dates>
      <SpendWrapper alignItems="flex-start">
        <SpendAmount>$2,000</SpendAmount>
        <SpendLabel>spend</SpendLabel>
      </SpendWrapper>
      <DeadlineLabel renderInMobile={false}>
        30 DAYS LEFT BEFORE PAYMENT
      </DeadlineLabel>
      <SpendWrapper alignItems="flex-end">
        <SpendAmount>$2,000</SpendAmount>
        <SpendLabel>payback</SpendLabel>
      </SpendWrapper>
    </Dates>
    <DeadlineLabel renderInMobile>30 DAYS LEFT BEFORE PAYMENT</DeadlineLabel>
  </Container>
);

export default PaymentSlider;
