import React from "react";
import styled from "@emotion/styled";
import { padding } from "polished";
import colors from "styles/colors";

const Wrapper = styled("div")(({ maxWidth }) => ({
  display: "flex",
  flexDirection: "column",
  maxWidth,
  paddingTop: 30,
  paddingBottom: 53,
  width: "100%",

  "@media (max-width: 1200px)": {
    ...padding(20),
  },
  "@media (max-width: 745px)": {
    maxWidth: "100%",
  },
}));

const Icon = styled("img")(({ iconWidth, iconHeight }) => ({
  width: iconWidth,
  height: iconHeight,
  marginBottom: 24,
}));

const Title = styled("h3")(({ light }) => ({
  fontSize: 32,
  lineHeight: "34px",
  fontWeight: 600,
  paddingBottom: 24,
  marginBottom: 0,
  color: light ? colors.white : colors.black,
}));

export const sizes = {
  medium: 48,
  large: 64,
};

const SimpleValueProp = ({
  icon,
  title,
  content,
  maxWidth = 300,
  light = false,
  size,
}) => (
  <Wrapper maxWidth={maxWidth}>
    <Icon src={icon} iconWidth={size} iconHeight={size} />
    <Title light={light}>{title}</Title>
    {content}
  </Wrapper>
);

export default SimpleValueProp;
